<template>
  <div class="container">
    <!-- VIEW CART -->
    <div class="cart-container">
      <div class="cart shadow-sm p-3 mb-5 bg-white rounded">
        <p href="#" class="cart-indicator">
          <i class="fas fa-shopping-cart "></i>
          {{ totalItems ? totalItems : 0 }} items in cart
        </p>
        <b-btn
          v-b-modal.modal2
          variant="secondary"
          class="add-cart-btn btn-block col-xs-12 mt-1"
          >View Cart</b-btn
        >
      </div>
    </div>
    <!-- END VIEW CART -->

    <!-- ROW WITH V-FOR -->
    <div class="row mt-5">
      <div
        class="list-group-item col-md-3 col-sm-6 col-xs-12 ml-5 mb-5 phone-item shadow-sm p-3 mb-5 bg-white rounded"
        v-for="product in items"
      >
        <img
          class="img-fluid m-auto mx-auto d-block"
          src="https://i0.wp.com/www.backpackpartners.com/wp-content/uploads/2015/11/phone-placeholder.jpg"
          alt="Product image"
        />
        <div>
          <h4 class="mb-5 text-center position-absolute">
            {{ product.name }} <br />
            <small>$00.00</small>
          </h4>
        </div>
        <div class="input-group">
          <button
            type="button"
            @click="decrementQuantity(product);"
            class="btn btn-danger mr-1"
          >
            <i class="fas fa-minus"></i>
          </button>
          <input
            class="form-control quantity"
            type="number"
            v-model="product.quantity"
            min="1"
          />
          <button
            type="button"
            @click="incrementQuantity(product);"
            class="btn btn-success ml-1"
          >
            <i class="fas fa-plus"></i>
          </button>
          <br />

          <b-btn
            v-b-modal.modal1
            variant="primary"
            class="add-cart-btn btn-block col-xs-12 mt-1"
            @click="addToCart(product);"
            >Add To Cart</b-btn
          >
        </div>
      </div>
    </div>
    <!-- END ROW -->

    <!-- MODAL -->
    <b-modal id="modal1" title="Your Cart">
      <div class="alert alert-success mt-1" role="alert">
        You successfully added {{ quantity }} item(s) to your cart.
      </div>
      <div v-for="item in cart">
        <div class="list-group">
          <p class="list-group-item">
            {{ item.name }}
            <span class="float-right">Quantity: {{ item.quantity }}</span>
          </p>
          <p class="text-right">Subtotal: <strong>$0.00</strong></p>
        </div>
      </div>
      <b-btn
        size="sm"
        class="float-right mt-3"
        variant="primary"
        @click="show = false;"
      >
        View Cart
      </b-btn>
    </b-modal>
    <b-modal id="modal2" title="Your Cart">
      <div v-for="item in cart">
        <div class="list-group">
          <p class="list-group-item">
            {{ item.name }}
            <span class="float-right">Quantity: {{ item.quantity }}</span>
          </p>
          <p class="text-right">Subtotal: <strong>$0.00</strong></p>
        </div>
      </div>
      <b-btn
        size="sm"
        class="float-right mt-3"
        variant="primary"
        @click="show = false;"
      >
        View Cart
      </b-btn>
    </b-modal>
    <!-- END MODAL -->
  </div>
</template>

<script>
import axios from "axios";
import _ from "lodash";
let config = {
  headers: {
    token: "CPU45I5SS6o5V8R3MHA50D45ECrZdgXKTnnH8AjmIphltw9lGDhYen8oHWdS"
  }
};
export default {
  data() {
    return {
      items: [],
      quantity: 0,
      cart: [],
      itemsInCart: [],
      subtotal: 0
    };
  },
  mounted() {
    axios({
      method: "post",
      url: "https://www.mobifix.com/api/products",
      headers: {
        "Content-Type": "application/json"
      },
      data: {
        token: "CPU45I5SS6o5V8R3MHA50D45ECrZdgXKTnnH8AjmIphltw9lGDhYen8oHWdS"
      }
    })
      .then(res => {
        //Give itesm in list quantity property
        let updatedList = (this.items = res.data.products.data);
        console.log(updatedList);
        for (let item of updatedList) {
          item.quantity = 1;
        }
        this.items = updatedList;
      })
      .catch(err => console.log(err));
  },
  methods: {
    addToCart(product) {
      //Check if item is in cart, if not push, if it is then update quantity
      if (product.quantity > 100) {
        alert("Quantity exceeds maximum limit of 100.");
        return;
      } else if (!this.itemsInCart.includes(product.id)) {
        this.cart.push(product);
        this.itemsInCart.push(product.id);
      } else {
        for (let x of this.cart) {
          if (x.id === product.id) {
            x.quantity += product.quantity;
          }
        }
      }
    },
    incrementQuantity(product) {
      this.quantity = product.quantity;
      product.quantity += 1;
    },
    decrementQuantity(product) {
      this.quantity = product.quantity;

      console.log(product.quantity);
      if (product.quantity >= 1) {
        product.quantity -= 1;
      }
    }
  },
  computed: {
    totalItems: function() {
      let total = this.cart.map(x => x.quantity).reduce((x, y) => x + y, 0);
      return total;
    }
  }
};
</script>

<style scoped lang="scss">
* {
  box-sizing: border-box;
}
img {
  height: 40%;
  margin: 0 auto;
}
.phone-item {
  height: 455px;
  overflow: hidden;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
.input-group {
  top: 25% !important;
}
.cart {
  border: 1px solid #888;
  width: 200px;
  font-size: 1.4rem;
  text-align: center;
  padding: 0.5em;
  margin-top: -3px;
}
.cart-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
</style>



